<template>
  <div class="app-container">
    <el-form
      :model="queryParams"
      ref="queryForm"
      :inline="true"
      v-show="showSearch"
      label-width="68px"
    >
      <el-form-item label="终止日期" prop="lelContractEnd">
        <el-date-picker
          clearable
          size="small"
          style="width: 150px"
          v-model="queryParams.lelContractStart"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="开始日期"
        >
        </el-date-picker
        >-
        <el-date-picker
          clearable
          size="small"
          style="width: 150px"
          v-model="queryParams.lelContractEnd"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="终止日期"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button
          type="cyan"
          icon="el-icon-search"
          size="mini"
          @click="handleQuery"
          >搜索</el-button
        >
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"
          >重置</el-button
        >
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:lease:add']"
          >新增</el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:lease:edit']"
          >修改</el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:lease:remove']"
          >删除</el-button
        >
      </el-col>
       <el-col :span="1.8">
        <el-button
          style="background:green"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:lease:remove']"
          >租金</el-button
        >
      </el-col>
       <el-col :span="1.8">
        <el-button
          style="background:yellow"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:lease:remove']"
          >水费</el-button
        >
      </el-col>
       <el-col :span="1.8">
        <el-button
          style="background:purple"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:lease:remove']"
          >电费</el-button
        >
      </el-col>
       <el-col :span="1.8">
        <el-button
          style="background:brown"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:lease:remove']"
          >押金</el-button
        >
      </el-col>
      <right-toolbar
        :showSearch.sync="showSearch"
        @queryTable="getList"
      ></right-toolbar>
    </el-row>

    <el-table
      v-loading="loading"
      :data="leaseList"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="入住人" align="center" prop="leLeaseUser" />
      <el-table-column label="营业执照" align="center" prop="leBussLicense" />
      <el-table-column label="法人代表" align="center" prop="leLegalPerson" />
      <el-table-column label="联系电话" align="center" prop="leLegalTell" />
      <el-table-column
        label="入驻日期"
        align="center"
        prop="leCheckIndate"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.leCheckIndate, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="合同附件"
        align="center"
        prop="leContractNearby"
      />
      <el-table-column label="月租金" align="center" prop="leLeaseMonth" />
      <el-table-column label="押金" align="center" prop="leLeaseDeposit" />
      <el-table-column label="支付方式" align="center" prop="lePayType" />
      <el-table-column
        label="终止日期"
        align="center"
        prop="lelContractEnd"
        width="180"
      >   
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.lelContractEnd, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      
      <el-table-column
        label="操作"
        align="center"
        class-name="small-padding fixed-width "
        width="150px"
        fixed="right"     
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:lease:edit']"
            >修改</el-button
          >
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:lease:remove']"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!-- 添加或修改【请填写功能名称】对话框 -->
    <el-dialog :title="title" :visible.sync="open" width="700px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <!-- <el-form-item label="房屋Id" prop="sysHouseId">
          <el-input v-model="form.sysHouseId" placeholder="请输入房屋Id" />
        </el-form-item> -->
        <el-row>
          <el-col :span="12">
            <el-form-item label="入住人" prop="leLeaseUser">
              <el-input v-model="form.leLeaseUser" placeholder="请输入入住人" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="营业执照" prop="leBussLicense">
              <el-input
                v-model="form.leBussLicense"
                placeholder="请输入营业执照"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="法人代表" prop="leLegalPerson">
              <el-input v-model="form.leLegalPerson" placeholder="请输入法人" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="法人电话" prop="leLegalTell">
              <el-input
                v-model="form.leLegalTell"
                placeholder="请输入法人电话"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="入驻日期" prop="leCheckIndate">
              <el-date-picker
                clearable
                size="small"
                style="width:100%"
                v-model="form.leCheckIndate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择入驻日期"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="合同附件" prop="leContractNearby">
              <el-input
                v-model="form.leContractNearby"
                placeholder="请输入合同附件"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="月租金" prop="leLeaseMonth">
              <el-input
                v-model="form.leLeaseMonth"
                placeholder="请输入月租金"
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="押金" prop="leLeaseDeposit">
              <el-input
                v-model="form.leLeaseDeposit"
                placeholder="请输入押金"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="支付方式" prop="lePayType" >
              <el-select
                v-model="form.lePayType"
                placeholder="请选择租金支付方式"
                style="width:100%"
              >
                <el-option label="押一付三月" value="1" />
                <el-option label="押一付半年" value="2" />
                <el-option label="押一付一年" value="3" />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="终止日期" prop="lelContractEnd">
              <el-date-picker
                clearable
                size="small"
                style="width:100%"
                v-model="form.lelContractEnd"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择合同终止日期"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm">确 定</el-button>
        <el-button @click="cancel">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  listLease,
  getLease,
  delLease,
  addLease,
  updateLease,
  exportLease,
} from "@/api/buss/house/lease";

export default {
  name: "Lease",
  components: {},
  data() {
    return {
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 【请填写功能名称】表格数据
      leaseList: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        sysHouseId: null,
        leLeaseUser: null,
        leBussLicense: null,
        leLegalPerson: null,
        leLegalTell: null,
        leCheckIndate: null,
        leContractNearby: null,
        leLeaseMonth: null,
        leLeaseDeposit: null,
        lePayType: null,
        lelContractEnd: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null,
      },
      // 表单参数
      form: {},
      // 表单校验
      rules: {},
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /** 查询【请填写功能名称】列表 */
    getList() {
      this.loading = true;
      listLease(this.queryParams).then((response) => {
        this.leaseList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 取消按钮
    cancel() {
      this.open = false;
      this.reset();
    },
    // 表单重置
    reset() {
      this.form = {
        id: null,
        sysHouseId: null,
        leLeaseUser: null,
        leBussLicense: null,
        leLegalPerson: null,
        leLegalTell: null,
        leCheckIndate: null,
        leContractNearby: null,
        leLeaseMonth: null,
        leLeaseDeposit: null,
        lePayType: null,
        lelContractEnd: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null,
      };
      this.resetForm("form");
    },
    /** 搜索按钮操作 */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /** 重置按钮操作 */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 多选框选中数据
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id);
      this.single = selection.length !== 1;
      this.multiple = !selection.length;
    },
    /** 新增按钮操作 */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "房屋楼租赁登记";
    },
    /** 修改按钮操作 */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids;
      getLease(id).then((response) => {
        this.form = response.data;
        this.open = true;
        this.title = "房屋楼租赁登记";
      });
    },
    /** 提交按钮 */
    submitForm() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.form.id != null) {
            updateLease(this.form).then((response) => {
              this.msgSuccess("修改成功");
              this.open = false;
              this.getList();
            });
          } else {
            addLease(this.form).then((response) => {
              this.msgSuccess("新增成功");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /** 删除按钮操作 */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm(
        '是否确认删除房屋楼租赁登记编号为"' + ids + '"的数据项?',
        "警告",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      )
        .then(function () {
          return delLease(ids);
        })
        .then(() => {
          this.getList();
          this.msgSuccess("删除成功");
        });
    },
    /** 导出按钮操作 */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm("是否确认导出所有【请填写功能名称】数据项?", "警告", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(function () {
          return exportLease(queryParams);
        })
        .then((response) => {
          this.download(response.msg);
        });
    },
  },
};
</script>
