import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listInspection(query) {
  return request({
    url: '/buss/safe/inspection/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getInspection(id) {
  return request({
    url: '/buss/safe/inspection/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addInspection(data) {
  return request({
    url: '/buss/safe/inspection',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updateInspection(data) {
  return request({
    url: '/buss/safe/inspection',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delInspection(id) {
  return request({
    url: '/buss/safe/inspection/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportInspection(query) {
  return request({
    url: '/buss/safe/inspection/export',
    method: 'get',
    params: query
  })
}