<template>
  <div class="app-container">
    <el-form :model="queryParams" ref="queryForm" :inline="true" v-show="showSearch" label-width="68px">
      <el-form-item label="设备类型" prop="xcDeviceType">
        <el-select v-model="queryParams.xcDeviceType" placeholder="请选择设备类型" clearable size="small">
          <el-option label="请选择字典生成" value="" />
        </el-select>
      </el-form-item>
      <el-form-item label="设备名称" prop="xcDeviceName">
        <el-input
          v-model="queryParams.xcDeviceName"
          placeholder="请输入设备名称"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="设备型号" prop="xcDeviceNorms">
        <el-input
          v-model="queryParams.xcDeviceNorms"
          placeholder="请输入设备型号"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="设备编号" prop="xcDeviceCode">
        <el-input
          v-model="queryParams.xcDeviceCode"
          placeholder="请输入设备编号"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="设备位置" prop="xcDevicePosition">
        <el-input
          v-model="queryParams.xcDevicePosition"
          placeholder="请输入设备位置"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="维修后设备状态：字典" prop="xcDeviceStatus">
        <el-select v-model="queryParams.xcDeviceStatus" placeholder="请选择维修后设备状态：字典" clearable size="small">
          <el-option label="请选择字典生成" value="" />
        </el-select>
      </el-form-item>
      <el-form-item label="故障时间" prop="xcFaultDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.xcFaultDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择故障时间">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="维护人" prop="xcRepairName">
        <el-input
          v-model="queryParams.xcRepairName"
          placeholder="请输入维护人"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="维修日期" prop="xcRepairDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.xcRepairDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择维修日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="创建时间" prop="sysCreateTime">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.sysCreateTime"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择创建时间">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="创建人" prop="sysCreatorId">
        <el-input
          v-model="queryParams.sysCreatorId"
          placeholder="请输入创建人"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="修改人" prop="sysModifiedId">
        <el-input
          v-model="queryParams.sysModifiedId"
          placeholder="请输入修改人"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="修改时间" prop="sysModifiedTime">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.sysModifiedTime"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择修改时间">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="cyan" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery">重置</el-button>
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:repair:add']"
        >新增</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:repair:edit']"
        >修改</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:repair:remove']"
        >删除</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="warning"
          icon="el-icon-download"
          size="mini"
          @click="handleExport"
          v-hasPermi="['system:repair:export']"
        >导出</el-button>
      </el-col>
	  <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar>
    </el-row>

    <el-table v-loading="loading" :data="repairList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="修改时间" align="center" prop="id" />
      <el-table-column label="设备类型" align="center" prop="xcDeviceType" />
      <el-table-column label="设备名称" align="center" prop="xcDeviceName" />
      <el-table-column label="设备型号" align="center" prop="xcDeviceNorms" />
      <el-table-column label="设备编号" align="center" prop="xcDeviceCode" />
      <el-table-column label="设备位置" align="center" prop="xcDevicePosition" />
      <el-table-column label="维修后设备状态：字典" align="center" prop="xcDeviceStatus" />
      <el-table-column label="故障时间" align="center" prop="xcFaultDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.xcFaultDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="维护人" align="center" prop="xcRepairName" />
      <el-table-column label="故障及维修内容描述" align="center" prop="xcRepairContent" />
      <el-table-column label="维修日期" align="center" prop="xcRepairDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.xcRepairDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="创建时间" align="center" prop="sysCreateTime" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.sysCreateTime, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="创建人" align="center" prop="sysCreatorId" />
      <el-table-column label="修改人" align="center" prop="sysModifiedId" />
      <el-table-column label="修改时间" align="center" prop="sysModifiedTime" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.sysModifiedTime, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:repair:edit']"
          >修改</el-button>
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:repair:remove']"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!-- 添加或修改【请填写功能名称】对话框 -->
    <el-dialog :title="title" :visible.sync="open" width="500px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="设备类型" prop="xcDeviceType">
          <el-select v-model="form.xcDeviceType" placeholder="请选择设备类型">
            <el-option label="请选择字典生成" value="" />
          </el-select>
        </el-form-item>
        <el-form-item label="设备名称" prop="xcDeviceName">
          <el-input v-model="form.xcDeviceName" placeholder="请输入设备名称" />
        </el-form-item>
        <el-form-item label="设备型号" prop="xcDeviceNorms">
          <el-input v-model="form.xcDeviceNorms" placeholder="请输入设备型号" />
        </el-form-item>
        <el-form-item label="设备编号" prop="xcDeviceCode">
          <el-input v-model="form.xcDeviceCode" placeholder="请输入设备编号" />
        </el-form-item>
        <el-form-item label="设备位置" prop="xcDevicePosition">
          <el-input v-model="form.xcDevicePosition" placeholder="请输入设备位置" />
        </el-form-item>
        <el-form-item label="维修后设备状态：字典">
          <el-radio-group v-model="form.xcDeviceStatus">
            <el-radio label="1">请选择字典生成</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="故障时间" prop="xcFaultDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.xcFaultDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择故障时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="维护人" prop="xcRepairName">
          <el-input v-model="form.xcRepairName" placeholder="请输入维护人" />
        </el-form-item>
        <el-form-item label="故障及维修内容描述">
          <editor v-model="form.xcRepairContent" :min-height="192"/>
        </el-form-item>
        <el-form-item label="维修日期" prop="xcRepairDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.xcRepairDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择维修日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="创建时间" prop="sysCreateTime">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.sysCreateTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择创建时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="创建人" prop="sysCreatorId">
          <el-input v-model="form.sysCreatorId" placeholder="请输入创建人" />
        </el-form-item>
        <el-form-item label="修改人" prop="sysModifiedId">
          <el-input v-model="form.sysModifiedId" placeholder="请输入修改人" />
        </el-form-item>
        <el-form-item label="修改时间" prop="sysModifiedTime">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.sysModifiedTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择修改时间">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm">确 定</el-button>
        <el-button @click="cancel">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { listRepair, getRepair, delRepair, addRepair, updateRepair, exportRepair } from "@/api/buss/device/repair";
import Editor from '@/components/Editor';

export default {
  name: "Repair",
  components: {
    Editor,
  },
  data() {
    return {
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 【请填写功能名称】表格数据
      repairList: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        xcDeviceType: null,
        xcDeviceName: null,
        xcDeviceNorms: null,
        xcDeviceCode: null,
        xcDevicePosition: null,
        xcDeviceStatus: null,
        xcFaultDate: null,
        xcRepairName: null,
        xcRepairContent: null,
        xcRepairDate: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null
      },
      // 表单参数
      form: {},
      // 表单校验
      rules: {
      }
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /** 查询【请填写功能名称】列表 */
    getList() {
      this.loading = true;
      listRepair(this.queryParams).then(response => {
        this.repairList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 取消按钮
    cancel() {
      this.open = false;
      this.reset();
    },
    // 表单重置
    reset() {
      this.form = {
        id: null,
        xcDeviceType: null,
        xcDeviceName: null,
        xcDeviceNorms: null,
        xcDeviceCode: null,
        xcDevicePosition: null,
        xcDeviceStatus: 0,
        xcFaultDate: null,
        xcRepairName: null,
        xcRepairContent: null,
        xcRepairDate: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null
      };
      this.resetForm("form");
    },
    /** 搜索按钮操作 */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /** 重置按钮操作 */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 多选框选中数据
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length!==1
      this.multiple = !selection.length
    },
    /** 新增按钮操作 */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "添加【请填写功能名称】";
    },
    /** 修改按钮操作 */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids
      getRepair(id).then(response => {
        this.form = response.data;
        this.open = true;
        this.title = "修改【请填写功能名称】";
      });
    },
    /** 提交按钮 */
    submitForm() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          if (this.form.id != null) {
            updateRepair(this.form).then(response => {
              this.msgSuccess("修改成功");
              this.open = false;
              this.getList();
            });
          } else {
            addRepair(this.form).then(response => {
              this.msgSuccess("新增成功");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /** 删除按钮操作 */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm('是否确认删除【请填写功能名称】编号为"' + ids + '"的数据项?', "警告", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(function() {
          return delRepair(ids);
        }).then(() => {
          this.getList();
          this.msgSuccess("删除成功");
        })
    },
    /** 导出按钮操作 */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm('是否确认导出所有【请填写功能名称】数据项?', "警告", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(function() {
          return exportRepair(queryParams);
        }).then(response => {
          this.download(response.msg);
        })
    }
  }
};
</script>
