<template>
  <div class="app-container">
    <el-form :model="queryParams" ref="queryForm" :inline="true" v-show="showSearch" label-width="68px">
      <el-form-item label="合同终止日期" prop="sysWorkshopId">
        <el-input
          v-model="queryParams.sysWorkshopId"
          placeholder="请输入合同终止日期"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="合同终止日期" prop="sysLeaseId">
        <el-input
          v-model="queryParams.sysLeaseId"
          placeholder="请输入合同终止日期"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="缴费开始日期" prop="rlStartDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.rlStartDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择缴费开始日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="缴费结束日期" prop="rlEndDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.rlEndDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择缴费结束日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="单价" prop="rlSquareMetrePrice">
        <el-input
          v-model="queryParams.rlSquareMetrePrice"
          placeholder="请输入单价"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="应缴金额" prop="rlPayShould">
        <el-input
          v-model="queryParams.rlPayShould"
          placeholder="请输入应缴金额"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="实际缴费" prop="rlPayReality">
        <el-input
          v-model="queryParams.rlPayReality"
          placeholder="请输入实际缴费"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="缴费日期" prop="rlPayDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.rlPayDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择缴费日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="缴费方式" prop="rlPayType">
        <el-select v-model="queryParams.rlPayType" placeholder="请选择缴费方式" clearable size="small">
          <el-option label="请选择字典生成" value="" />
        </el-select>
      </el-form-item>
      <el-form-item label="合同终止日期" prop="rlContractEnd">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.rlContractEnd"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择合同终止日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="cyan" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery">重置</el-button>
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:realty:add']"
        >新增</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:realty:edit']"
        >修改</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:realty:remove']"
        >删除</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="warning"
          icon="el-icon-download"
          size="mini"
          @click="handleExport"
          v-hasPermi="['system:realty:export']"
        >导出</el-button>
      </el-col>
	  <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar>
    </el-row>

    <el-table v-loading="loading" :data="realtyList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="合同终止日期" align="center" prop="id" />
      <el-table-column label="合同终止日期" align="center" prop="sysWorkshopId" />
      <el-table-column label="合同终止日期" align="center" prop="sysLeaseId" />
      <el-table-column label="缴费开始日期" align="center" prop="rlStartDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.rlStartDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="缴费结束日期" align="center" prop="rlEndDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.rlEndDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="单价" align="center" prop="rlSquareMetrePrice" />
      <el-table-column label="应缴金额" align="center" prop="rlPayShould" />
      <el-table-column label="实际缴费" align="center" prop="rlPayReality" />
      <el-table-column label="缴费日期" align="center" prop="rlPayDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.rlPayDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="缴费方式" align="center" prop="rlPayType" />
      <el-table-column label="合同终止日期" align="center" prop="rlContractEnd" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.rlContractEnd, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:realty:edit']"
          >修改</el-button>
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:realty:remove']"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!-- 添加或修改【请填写功能名称】对话框 -->
    <el-dialog :title="title" :visible.sync="open" width="500px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="合同终止日期" prop="sysWorkshopId">
          <el-input v-model="form.sysWorkshopId" placeholder="请输入合同终止日期" />
        </el-form-item>
        <el-form-item label="合同终止日期" prop="sysLeaseId">
          <el-input v-model="form.sysLeaseId" placeholder="请输入合同终止日期" />
        </el-form-item>
        <el-form-item label="缴费开始日期" prop="rlStartDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.rlStartDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择缴费开始日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="缴费结束日期" prop="rlEndDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.rlEndDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择缴费结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="单价" prop="rlSquareMetrePrice">
          <el-input v-model="form.rlSquareMetrePrice" placeholder="请输入单价" />
        </el-form-item>
        <el-form-item label="应缴金额" prop="rlPayShould">
          <el-input v-model="form.rlPayShould" placeholder="请输入应缴金额" />
        </el-form-item>
        <el-form-item label="实际缴费" prop="rlPayReality">
          <el-input v-model="form.rlPayReality" placeholder="请输入实际缴费" />
        </el-form-item>
        <el-form-item label="缴费日期" prop="rlPayDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.rlPayDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择缴费日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="缴费方式" prop="rlPayType">
          <el-select v-model="form.rlPayType" placeholder="请选择缴费方式">
            <el-option label="请选择字典生成" value="" />
          </el-select>
        </el-form-item>
        <el-form-item label="合同终止日期" prop="rlContractEnd">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.rlContractEnd"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择合同终止日期">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm">确 定</el-button>
        <el-button @click="cancel">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { listRealty, getRealty, delRealty, addRealty, updateRealty, exportRealty } from "@/api/buss/workshop/realty";

export default {
  name: "Realty",
  components: {
  },
  data() {
    return {
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 【请填写功能名称】表格数据
      realtyList: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        sysWorkshopId: null,
        sysLeaseId: null,
        rlStartDate: null,
        rlEndDate: null,
        rlSquareMetrePrice: null,
        rlPayShould: null,
        rlPayReality: null,
        rlPayDate: null,
        rlPayType: null,
        rlContractEnd: null
      },
      // 表单参数
      form: {},
      // 表单校验
      rules: {
      }
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /** 查询【请填写功能名称】列表 */
    getList() {
      this.loading = true;
      listRealty(this.queryParams).then(response => {
        this.realtyList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 取消按钮
    cancel() {
      this.open = false;
      this.reset();
    },
    // 表单重置
    reset() {
      this.form = {
        id: null,
        sysWorkshopId: null,
        sysLeaseId: null,
        rlStartDate: null,
        rlEndDate: null,
        rlSquareMetrePrice: null,
        rlPayShould: null,
        rlPayReality: null,
        rlPayDate: null,
        rlPayType: null,
        rlContractEnd: null
      };
      this.resetForm("form");
    },
    /** 搜索按钮操作 */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /** 重置按钮操作 */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 多选框选中数据
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length!==1
      this.multiple = !selection.length
    },
    /** 新增按钮操作 */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "添加【请填写功能名称】";
    },
    /** 修改按钮操作 */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids
      getRealty(id).then(response => {
        this.form = response.data;
        this.open = true;
        this.title = "修改【请填写功能名称】";
      });
    },
    /** 提交按钮 */
    submitForm() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          if (this.form.id != null) {
            updateRealty(this.form).then(response => {
              this.msgSuccess("修改成功");
              this.open = false;
              this.getList();
            });
          } else {
            addRealty(this.form).then(response => {
              this.msgSuccess("新增成功");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /** 删除按钮操作 */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm('是否确认删除【请填写功能名称】编号为"' + ids + '"的数据项?', "警告", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(function() {
          return delRealty(ids);
        }).then(() => {
          this.getList();
          this.msgSuccess("删除成功");
        })
    },
    /** 导出按钮操作 */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm('是否确认导出所有【请填写功能名称】数据项?', "警告", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(function() {
          return exportRealty(queryParams);
        }).then(response => {
          this.download(response.msg);
        })
    }
  }
};
</script>
