import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listOnduty(query) {
  return request({
    url: '/buss/base/onduty/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getOnduty(id) {
  return request({
    url: '/buss/base/onduty/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addOnduty(data) {
  return request({
    url: '/buss/base/onduty',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updateOnduty(data) {
  return request({
    url: '/buss/base/onduty',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delOnduty(id) {
  return request({
    url: '/buss/base/onduty/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportOnduty(query) {
  return request({
    url: '/buss/base/onduty/export',
    method: 'get',
    params: query
  })
}