import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listOther(query) {
  return request({
    url: '/buss/house/other/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getOther(id) {
  return request({
    url: '/buss/house/other/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addOther(data) {
  return request({
    url: '/buss/house/other',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updateOther(data) {
  return request({
    url: '/buss/house/other',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delOther(id) {
  return request({
    url: '/buss/house/other/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportOther(query) {
  return request({
    url: '/buss/house/other/export',
    method: 'get',
    params: query
  })
}