import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listPaper(query) {
  return request({
    url: '/buss/base/paper/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getPaper(id) {
  return request({
    url: '/buss/base/paper/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addPaper(data) {
  return request({
    url: '/buss/base/paper',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updatePaper(data) {
  return request({
    url: '/buss/base/paper',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delPaper(id) {
  return request({
    url: '/buss/base/paper/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportPaper(query) {
  return request({
    url: '/buss/base/paper/export',
    method: 'get',
    params: query
  })
}