import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listElectricity(query) {
  return request({
    url: '/buss/house/electricity/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getElectricity(id) {
  return request({
    url: '/buss/house/electricity/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addElectricity(data) {
  return request({
    url: '/buss/house/electricity',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updateElectricity(data) {
  return request({
    url: '/buss/house/electricity',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delElectricity(id) {
  return request({
    url: '/buss/house/electricity/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportElectricity(query) {
  return request({
    url: '/buss/house/electricity/export',
    method: 'get',
    params: query
  })
}