<template>
  <div class="app-container">
    <el-form :model="queryParams" ref="queryForm" :inline="true" v-show="showSearch" label-width="68px">
      <el-form-item label="厂房名称" prop="cpName">
        <el-input
          v-model="queryParams.cpName"
          placeholder="请输入厂房名称"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="厂房面积" prop="cpArea">
        <el-input
          v-model="queryParams.cpArea"
          placeholder="请输入厂房面积"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="厂房用途" prop="cpNature">
        <el-input
          v-model="queryParams.cpNature"
          placeholder="请输入厂房用途"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="厂房状态" prop="cpStatus">
        <el-select v-model="queryParams.cpStatus" placeholder="请选择厂房状态" clearable size="small">
          <el-option label="请选择字典生成" value="" />
        </el-select>
      </el-form-item>
      <el-form-item label="产权人" prop="cpPropertyOwner">
        <el-input
          v-model="queryParams.cpPropertyOwner"
          placeholder="请输入产权人"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="产权证" prop="cpCertificateTitle">
        <el-input
          v-model="queryParams.cpCertificateTitle"
          placeholder="请输入产权证"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="更新日期" prop="cpUpdateDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.cpUpdateDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择更新日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="cyan" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery">重置</el-button>
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:assets:add']"
        >新增</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:assets:edit']"
        >修改</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:assets:remove']"
        >删除</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="warning"
          icon="el-icon-download"
          size="mini"
          @click="handleExport"
          v-hasPermi="['system:assets:export']"
        >导出</el-button>
      </el-col>
	  <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar>
    </el-row>

    <el-table v-loading="loading" :data="assetsList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="厂房简介" align="center" prop="id" />
      <el-table-column label="厂房名称" align="center" prop="cpName" />
      <el-table-column label="厂房面积" align="center" prop="cpArea" />
      <el-table-column label="厂房用途" align="center" prop="cpNature" />
      <el-table-column label="厂房状态" align="center" prop="cpStatus" />
      <el-table-column label="产权人" align="center" prop="cpPropertyOwner" />
      <el-table-column label="产权证" align="center" prop="cpCertificateTitle" />
      <el-table-column label="更新日期" align="center" prop="cpUpdateDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.cpUpdateDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="厂房照片" align="center" prop="cpImage" />
      <el-table-column label="厂房简介" align="center" prop="cpContent" />
      <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:assets:edit']"
          >修改</el-button>
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:assets:remove']"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!-- 添加或修改【请填写功能名称】对话框 -->
    <el-dialog :title="title" :visible.sync="open" width="500px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="厂房名称" prop="cpName">
          <el-input v-model="form.cpName" placeholder="请输入厂房名称" />
        </el-form-item>
        <el-form-item label="厂房面积" prop="cpArea">
          <el-input v-model="form.cpArea" placeholder="请输入厂房面积" />
        </el-form-item>
        <el-form-item label="厂房用途" prop="cpNature">
          <el-input v-model="form.cpNature" placeholder="请输入厂房用途" />
        </el-form-item>
        <el-form-item label="厂房状态">
          <el-radio-group v-model="form.cpStatus">
            <el-radio label="1">请选择字典生成</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="产权人" prop="cpPropertyOwner">
          <el-input v-model="form.cpPropertyOwner" placeholder="请输入产权人" />
        </el-form-item>
        <el-form-item label="产权证" prop="cpCertificateTitle">
          <el-input v-model="form.cpCertificateTitle" placeholder="请输入产权证" />
        </el-form-item>
        <el-form-item label="更新日期" prop="cpUpdateDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.cpUpdateDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择更新日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="厂房照片">
          <uploadImage v-model="form.cpImage"/>
        </el-form-item>
        <el-form-item label="厂房简介">
          <editor v-model="form.cpContent" :min-height="192"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm">确 定</el-button>
        <el-button @click="cancel">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { listAssets, getAssets, delAssets, addAssets, updateAssets, exportAssets } from "@/api/buss/workshop/assets";
import UploadImage from '@/components/UploadImage';
import Editor from '@/components/Editor';

export default {
  name: "Assets",
  components: {
    UploadImage,
    Editor,
  },
  data() {
    return {
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 【请填写功能名称】表格数据
      assetsList: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        cpName: null,
        cpArea: null,
        cpNature: null,
        cpStatus: null,
        cpPropertyOwner: null,
        cpCertificateTitle: null,
        cpUpdateDate: null,
        cpImage: null,
        cpContent: null
      },
      // 表单参数
      form: {},
      // 表单校验
      rules: {
      }
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /** 查询【请填写功能名称】列表 */
    getList() {
      this.loading = true;
      listAssets(this.queryParams).then(response => {
        this.assetsList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 取消按钮
    cancel() {
      this.open = false;
      this.reset();
    },
    // 表单重置
    reset() {
      this.form = {
        id: null,
        cpName: null,
        cpArea: null,
        cpNature: null,
        cpStatus: 0,
        cpPropertyOwner: null,
        cpCertificateTitle: null,
        cpUpdateDate: null,
        cpImage: null,
        cpContent: null
      };
      this.resetForm("form");
    },
    /** 搜索按钮操作 */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /** 重置按钮操作 */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 多选框选中数据
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length!==1
      this.multiple = !selection.length
    },
    /** 新增按钮操作 */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "添加【请填写功能名称】";
    },
    /** 修改按钮操作 */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids
      getAssets(id).then(response => {
        this.form = response.data;
        this.open = true;
        this.title = "修改【请填写功能名称】";
      });
    },
    /** 提交按钮 */
    submitForm() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          if (this.form.id != null) {
            updateAssets(this.form).then(response => {
              this.msgSuccess("修改成功");
              this.open = false;
              this.getList();
            });
          } else {
            addAssets(this.form).then(response => {
              this.msgSuccess("新增成功");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /** 删除按钮操作 */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm('是否确认删除【请填写功能名称】编号为"' + ids + '"的数据项?', "警告", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(function() {
          return delAssets(ids);
        }).then(() => {
          this.getList();
          this.msgSuccess("删除成功");
        })
    },
    /** 导出按钮操作 */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm('是否确认导出所有【请填写功能名称】数据项?', "警告", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(function() {
          return exportAssets(queryParams);
        }).then(response => {
          this.download(response.msg);
        })
    }
  }
};
</script>
