<template>
  <div class="app-container">
    <el-form :model="queryParams" ref="queryForm" :inline="true" v-show="showSearch" label-width="68px">
      <el-form-item label="巡更器名称" prop="xcInspectionName">
        <el-input
          v-model="queryParams.xcInspectionName"
          placeholder="请输入巡更器名称"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="巡更日期" prop="xcInspectionDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.xcInspectionDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择巡更日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="巡更点位" prop="xcInspectionPosition">
        <el-input
          v-model="queryParams.xcInspectionPosition"
          placeholder="请输入巡更点位"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="巡更时间" prop="xcInspectionTime">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.xcInspectionTime"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择巡更时间">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="考核状态：字典" prop="xcExamineStatus">
        <el-select v-model="queryParams.xcExamineStatus" placeholder="请选择考核状态：字典" clearable size="small">
          <el-option label="请选择字典生成" value="" />
        </el-select>
      </el-form-item>
      <el-form-item label="创建时间" prop="sysCreateTime">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.sysCreateTime"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择创建时间">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="创建人" prop="sysCreatorId">
        <el-input
          v-model="queryParams.sysCreatorId"
          placeholder="请输入创建人"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="修改人" prop="sysModifiedId">
        <el-input
          v-model="queryParams.sysModifiedId"
          placeholder="请输入修改人"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="修改时间" prop="sysModifiedTime">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.sysModifiedTime"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择修改时间">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="cyan" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery">重置</el-button>
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:inspection:add']"
        >新增</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:inspection:edit']"
        >修改</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:inspection:remove']"
        >删除</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="warning"
          icon="el-icon-download"
          size="mini"
          @click="handleExport"
          v-hasPermi="['system:inspection:export']"
        >导出</el-button>
      </el-col>
	  <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar>
    </el-row>

    <el-table v-loading="loading" :data="inspectionList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="修改时间" align="center" prop="id" />
      <el-table-column label="巡更器名称" align="center" prop="xcInspectionName" />
      <el-table-column label="巡更日期" align="center" prop="xcInspectionDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.xcInspectionDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="巡更点位" align="center" prop="xcInspectionPosition" />
      <el-table-column label="巡更时间" align="center" prop="xcInspectionTime" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.xcInspectionTime, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="考核状态：字典" align="center" prop="xcExamineStatus" />
      <el-table-column label="创建时间" align="center" prop="sysCreateTime" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.sysCreateTime, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="创建人" align="center" prop="sysCreatorId" />
      <el-table-column label="修改人" align="center" prop="sysModifiedId" />
      <el-table-column label="修改时间" align="center" prop="sysModifiedTime" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.sysModifiedTime, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:inspection:edit']"
          >修改</el-button>
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:inspection:remove']"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!-- 添加或修改【请填写功能名称】对话框 -->
    <el-dialog :title="title" :visible.sync="open" width="500px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="巡更器名称" prop="xcInspectionName">
          <el-input v-model="form.xcInspectionName" placeholder="请输入巡更器名称" />
        </el-form-item>
        <el-form-item label="巡更日期" prop="xcInspectionDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.xcInspectionDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择巡更日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="巡更点位" prop="xcInspectionPosition">
          <el-input v-model="form.xcInspectionPosition" placeholder="请输入巡更点位" />
        </el-form-item>
        <el-form-item label="巡更时间" prop="xcInspectionTime">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.xcInspectionTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择巡更时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="考核状态：字典">
          <el-radio-group v-model="form.xcExamineStatus">
            <el-radio label="1">请选择字典生成</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="创建时间" prop="sysCreateTime">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.sysCreateTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择创建时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="创建人" prop="sysCreatorId">
          <el-input v-model="form.sysCreatorId" placeholder="请输入创建人" />
        </el-form-item>
        <el-form-item label="修改人" prop="sysModifiedId">
          <el-input v-model="form.sysModifiedId" placeholder="请输入修改人" />
        </el-form-item>
        <el-form-item label="修改时间" prop="sysModifiedTime">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.sysModifiedTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择修改时间">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm">确 定</el-button>
        <el-button @click="cancel">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { listInspection, getInspection, delInspection, addInspection, updateInspection, exportInspection } from "@/api/buss/safe/inspection";

export default {
  name: "Inspection",
  components: {
  },
  data() {
    return {
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 【请填写功能名称】表格数据
      inspectionList: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        xcInspectionName: null,
        xcInspectionDate: null,
        xcInspectionPosition: null,
        xcInspectionTime: null,
        xcExamineStatus: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null
      },
      // 表单参数
      form: {},
      // 表单校验
      rules: {
      }
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /** 查询【请填写功能名称】列表 */
    getList() {
      this.loading = true;
      listInspection(this.queryParams).then(response => {
        this.inspectionList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 取消按钮
    cancel() {
      this.open = false;
      this.reset();
    },
    // 表单重置
    reset() {
      this.form = {
        id: null,
        xcInspectionName: null,
        xcInspectionDate: null,
        xcInspectionPosition: null,
        xcInspectionTime: null,
        xcExamineStatus: 0,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null
      };
      this.resetForm("form");
    },
    /** 搜索按钮操作 */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /** 重置按钮操作 */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 多选框选中数据
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length!==1
      this.multiple = !selection.length
    },
    /** 新增按钮操作 */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "添加【请填写功能名称】";
    },
    /** 修改按钮操作 */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids
      getInspection(id).then(response => {
        this.form = response.data;
        this.open = true;
        this.title = "修改【请填写功能名称】";
      });
    },
    /** 提交按钮 */
    submitForm() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          if (this.form.id != null) {
            updateInspection(this.form).then(response => {
              this.msgSuccess("修改成功");
              this.open = false;
              this.getList();
            });
          } else {
            addInspection(this.form).then(response => {
              this.msgSuccess("新增成功");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /** 删除按钮操作 */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm('是否确认删除【请填写功能名称】编号为"' + ids + '"的数据项?', "警告", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(function() {
          return delInspection(ids);
        }).then(() => {
          this.getList();
          this.msgSuccess("删除成功");
        })
    },
    /** 导出按钮操作 */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm('是否确认导出所有【请填写功能名称】数据项?', "警告", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(function() {
          return exportInspection(queryParams);
        }).then(response => {
          this.download(response.msg);
        })
    }
  }
};
</script>
