import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listWater(query) {
  return request({
    url: '/buss/workshop/water/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getWater(id) {
  return request({
    url: '/buss/workshop/water/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addWater(data) {
  return request({
    url: '/buss/workshop/water',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updateWater(data) {
  return request({
    url: '/buss/workshop/water',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delWater(id) {
  return request({
    url: '/buss/workshop/water/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportWater(query) {
  return request({
    url: '/buss/workshop/water/export',
    method: 'get',
    params: query
  })
}