import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listContacts(query) {
  return request({
    url: '/buss/base/contacts/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getContacts(id) {
  return request({
    url: '/buss/base/contacts/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addContacts(data) {
  return request({
    url: '/buss/base/contacts',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updateContacts(data) {
  return request({
    url: '/buss/base/contacts',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delContacts(id) {
  return request({
    url: '/buss/base/contacts/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportContacts(query) {
  return request({
    url: '/buss/base/contacts/export',
    method: 'get',
    params: query
  })
}