import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listLease(query) {
  return request({
    url: '/buss/workshop/lease/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getLease(id) {
  return request({
    url: '/buss/workshop/lease/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addLease(data) {
  return request({
    url: '/buss/workshop/lease',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updateLease(data) {
  return request({
    url: '/buss/workshop/lease',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delLease(id) {
  return request({
    url: '/buss/workshop/lease/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportLease(query) {
  return request({
    url: '/system/lease/export',
    method: 'get',
    params: query
  })
}