import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listDevice(query) {
  return request({
    url: '/buss/device/device/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getDevice(id) {
  return request({
    url: '/buss/device/device/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addDevice(data) {
  return request({
    url: '/buss/device/device',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updateDevice(data) {
  return request({
    url: '/buss/device/device',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delDevice(id) {
  return request({
    url: '/buss/device/device/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportDevice(query) {
  return request({
    url: '/buss/device/device/export',
    method: 'get',
    params: query
  })
}