<template>
  <div class="app-container">
    <el-form
      :model="queryParams"
      ref="queryForm"
      :inline="true"
      v-show="showSearch"
      label-width="68px"
    >
      <el-form-item label="事件名称" prop="ceName">
        <el-input
          v-model="queryParams.ceName"
          placeholder="请输入事件名称"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="发生日期" prop="ceDate">
        <el-date-picker
          clearable
          size="small"
          style="width: 150px"
          v-model="queryParams.ceDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择开始日期"
        >
        </el-date-picker
        >-
        <el-date-picker
          clearable
          size="small"
          style="width: 150px"
          v-model="queryParams.ceDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择结束日期"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button
          type="cyan"
          icon="el-icon-search"
          size="mini"
          @click="handleQuery"
          >搜索</el-button
        >
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"
          >重置</el-button
        >
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['buss:emergency:event:add']"
          >新增</el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['buss:emergency:event:edit']"
          >修改</el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['buss:emergency:event:remove']"
          >删除</el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="warning"
          size="mini"
          :disabled="single"
          @click="handleExport"
          >跟踪</el-button
        >
      </el-col>
      <right-toolbar
        :showSearch.sync="showSearch"
        @queryTable="getList"
      ></right-toolbar>
    </el-row>

    <el-table
      v-loading="loading"
      :data="eventList"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="事件名称" align="center" prop="ceName" />
      <el-table-column label="事件类型" align="center" prop="ceType" />
      <el-table-column
        label="发生时间"
        align="center"
        prop="ceDate"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.ceDate, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column label="现场照片" align="center" prop="ceImages" />
      <el-table-column label="事件地点" align="center" prop="ceAddress" />
      <el-table-column label="事件描述" align="center" prop="ceContent" />
      <el-table-column label="上传人" align="center" prop="ceUploadUser" />
      <el-table-column
        label="操作"
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            >修改</el-button
          >
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            >删除</el-button>
            <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleExport(scope.row)"
            >跟踪</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!-- 添加或修改【请填写功能名称】对话框 -->
    <el-dialog :title="title" :visible.sync="open" width="700px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="事件名称" prop="ceName">
              <el-input v-model="form.ceName" placeholder="请输入事件名称" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="事件类型" prop="ceType">
              <el-select
                v-model="form.ceType"
                placeholder="请选择事件类型"
                style="width: 250px"
              >
                <el-option label="请选择字典生成" value="" />
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="发生日期" prop="ceDate">
              <el-date-picker
                clearable
                size="small"
                style="width: 250px"
                v-model="form.ceDate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择发生日期"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="上传人" prop="ceUploadUser">
              <el-input
                v-model="form.ceUploadUser"
                placeholder="请输入上传人"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="发生地点" prop="ceAddress">
              <el-input v-model="form.ceAddress" placeholder="请输入发生地点" />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="现场图片" prop="ceImages">
              <el-input v-model="form.ceImages" placeholder="请输入现场图片" />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="事件内容">
              <el-input
                type="textarea"
                v-model="form.ceContent"
                :autosize="{ minRows: 5, maxRows: 10 }"
                maxlength="2000"
                show-word-limit
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm">确 定</el-button>
        <el-button @click="cancel">取 消</el-button>
      </div>
    </el-dialog>
    <!-- 跟踪预案管理 -->
    <el-dialog :title="title" :visible.sync="add" width="500px" append-to-body>
      <el-form ref="form" :model="addform" :rules="rules" label-width="80px">
        <el-form-item label="处理日期" prop="ctDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="addform.ctDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择处理日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="处理内容">
           <el-input type="textarea" v-model="addform.ctContent"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="trackSubmit">确 定</el-button>
        <el-button @click="add=false">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  listEvent,
  getEvent,
  delEvent,
  addEvent,
  updateEvent,
  exportEvent,
} from "@/api/buss/emergency/event";
import {
  addTrack
} from "@/api/buss/emergency/track";



export default {
  name: "Event",
  components: {
  },
  data() {
    return {
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 【请填写功能名称】表格数据
      eventList: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      add:false,
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        ceName: null,
        ceType: null,
        ceDate: null,
        ceAddress: null,
        ceImages: null,
        ceContent: null,
        ceUploadUser: null,
      },
      // 表单参数
      form: {},
      addform:{
        sysMainId:null,
        ctType:1,
      },
      // 表单校验
      rules: {},
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /** 查询【请填写功能名称】列表 */
    getList() {
      this.loading = true;
      listEvent(this.queryParams).then((response) => {
        this.eventList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 取消按钮
    cancel() {
      this.open = false;
      this.reset();
    },
    // 表单重置
    reset() {
      this.form = {
        ceName: null,
        ceType: null,
        ceDate: null,
        ceAddress: null,
        ceImages: null,
        ceContent: null,
        ceUploadUser: null,
        id: null,
      };
      this.resetForm("form");
    },
    /** 搜索按钮操作 */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /** 重置按钮操作 */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 多选框选中数据
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id);
      this.single = selection.length !== 1;
      this.multiple = !selection.length;
    },
    /** 新增按钮操作 */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "事件登记";
    },
    /** 修改按钮操作 */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids;
      getEvent(id).then((response) => {
        this.form = response.data;
        this.open = true;
        this.title = "事件登记";
      });
    },
    /** 提交按钮 */
    trackSubmit(){
      debugger
      if (this.addform.sysMainId != null) {
        addTrack(this.addform).then((response) => {
          this.msgSuccess("添加成功");
          this.add = false;
          this.getList();
        });
      }
    },
    submitForm() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.form.id != null) {
            updateEvent(this.form).then((response) => {
              this.msgSuccess("修改成功");
              this.open = false;
              this.getList();
            });
          } else {
            addEvent(this.form).then((response) => {
              this.msgSuccess("新增成功");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /** 删除按钮操作 */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm(
        '是否确认删除【请填写功能名称】编号为"' + ids + '"的数据项?',
        "警告",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      )
        .then(function () {
          return delEvent(ids);
        })
        .then(() => {
          this.getList();
          this.msgSuccess("删除成功");
        });
    },
    /** 导出按钮操作 */
    handleExport(row) {
      debugger
      this.add=true;
      this.title = "跟踪登记";
      this.addform.sysMainId=row.id
    },
  },
};
</script>
