<template>
  <div class="app-container">
    <el-form
      :model="queryParams"
      ref="queryForm"
      :inline="true"
      v-show="showSearch"
      label-width="68px"
    >
      <el-form-item label="房间号" prop="sysHouseId">
        <el-input
          v-model="queryParams.sysHouseId"
          placeholder="请输入房间号"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="户型" prop="sysHouseId">
        <el-select v-model="form.lePayType" placeholder="请选择户型">
          <el-option label="户型一" value="1" />
          <el-option label="户型二" value="2" />
          <el-option label="户型三" value="3" />
        </el-select>
      </el-form-item>
      <el-form-item label="终止日期" prop="elContractEnd">
        <el-date-picker
          clearable
          size="small"
          style="width: 150px"
          v-model="queryParams.elContractStart"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="开始日期"
        >
        </el-date-picker
        >-
        <el-date-picker
          clearable
          size="small"
          style="width: 150px"
          v-model="queryParams.elContractEnd"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="终止日期"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button
          type="cyan"
          icon="el-icon-search"
          size="mini"
          @click="handleQuery"
          >搜索</el-button
        >
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"
          >重置</el-button
        >
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:water:add']"
          >新增</el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:water:edit']"
          >修改</el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:water:remove']"
          >删除</el-button
        >
      </el-col>
      <right-toolbar
        :showSearch.sync="showSearch"
        @queryTable="getList"
      ></right-toolbar>
    </el-row>

    <el-table
      v-loading="loading"
      :data="waterList"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="本期抄数" align="center" prop="elTonCurrrent" />
      <el-table-column label="上期抄数" align="center" prop="elTonPre" />
      <el-table-column
        label="抄表日期"
        align="center"
        prop="elTonDate"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.elTonDate, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column label="用水量" align="center" prop="elTonSum" />
      <el-table-column label="水费单价" align="center" prop="elTonPrice" />
      <el-table-column label="应缴金额" align="center" prop="elPayShould" />
      <el-table-column label="实际缴费" align="center" prop="elPayReality" />
      <el-table-column
        label="缴费日期"
        align="center"
        prop="elPayDate"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.elPayDate, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column label="支付方式" align="center" prop="elPayType" />
      <el-table-column
        label="操作"
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:water:edit']"
            >修改</el-button
          >
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:water:remove']"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!-- 添加或修改【请填写功能名称】对话框 -->
    <el-dialog :title="title" :visible.sync="open" width="700px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="上期抄数" prop="elTonPre">
              <el-input v-model="form.elTonPre" placeholder="请输入上期抄数" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="本期抄数" prop="elTonCurrrent">
              <el-input
                v-model="form.elTonCurrrent"
                placeholder="请输入本期抄数"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="抄表日期" prop="elTonDate">
              <el-date-picker
                clearable
                size="small"
                style="width: 100%"
                v-model="form.elTonDate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择抄表日期"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="用水量" prop="elTonSum">
              <el-input v-model="form.elTonSum" placeholder="请输入用水量" />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="水费单价" prop="elTonPrice">
              <el-input
                v-model="form.elTonPrice"
                placeholder="请输入水费单价"
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="应缴金额" prop="elPayShould">
              <el-input
                v-model="form.elPayShould"
                placeholder="请输入应缴金额"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="实际缴费" prop="elPayReality">
              <el-input
                v-model="form.elPayReality"
                placeholder="请输入实际缴费"
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="缴费日期" prop="elPayDate">
              <el-date-picker
                clearable
                size="small"
                style="width: 100%"
                v-model="form.elPayDate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择缴费日期"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="支付方式" prop="elPayType">
              <el-select v-model="form.elPayType" placeholder="请选择缴费方式" style="width: 100%">
                <el-option label="银行转账" value="1" />
                <el-option label="微信转账" value="2" />
                <el-option label="现付" value="3" />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="终止日期" prop="elContractEnd">
              <el-date-picker
                clearable
                size="small"
                style="width: 100%"
                v-model="form.elContractEnd"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择终止日期"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm">确 定</el-button>
        <el-button @click="cancel">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  listWater,
  getWater,
  delWater,
  addWater,
  updateWater,
  exportWater,
} from "@/api/buss/house/water";

export default {
  name: "Water",
  components: {},
  data() {
    return {
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 【请填写功能名称】表格数据
      waterList: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        sysHouseId: null,
        sysLeaseId: null,
        elTonCurrrent: null,
        elTonPre: null,
        elTonDate: null,
        elTonSum: null,
        elTonPrice: null,
        elPayShould: null,
        elPayReality: null,
        elPayDate: null,
        elPayType: null,
        elContractEnd: null,
      },
      // 表单参数
      form: {},
      // 表单校验
      rules: {},
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /** 查询【请填写功能名称】列表 */
    getList() {
      this.loading = true;
      listWater(this.queryParams).then((response) => {
        this.waterList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 取消按钮
    cancel() {
      this.open = false;
      this.reset();
    },
    // 表单重置
    reset() {
      this.form = {
        id: null,
        sysHouseId: null,
        sysLeaseId: null,
        elTonCurrrent: null,
        elTonPre: null,
        elTonDate: null,
        elTonSum: null,
        elTonPrice: null,
        elPayShould: null,
        elPayReality: null,
        elPayDate: null,
        elPayType: null,
        elContractEnd: null,
      };
      this.resetForm("form");
    },
    /** 搜索按钮操作 */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /** 重置按钮操作 */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 多选框选中数据
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id);
      this.single = selection.length !== 1;
      this.multiple = !selection.length;
    },
    /** 新增按钮操作 */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "物业费用登记";
    },
    /** 修改按钮操作 */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids;
      getWater(id).then((response) => {
        this.form = response.data;
        this.open = true;
        this.title = "物业费用登记";
      });
    },
    /** 提交按钮 */
    submitForm() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.form.id != null) {
            updateWater(this.form).then((response) => {
              this.msgSuccess("修改成功");
              this.open = false;
              this.getList();
            });
          } else {
            addWater(this.form).then((response) => {
              this.msgSuccess("新增成功");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /** 删除按钮操作 */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm(
        '是否确认删除物业费用登记编号为"' + ids + '"的数据项?',
        "警告",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      )
        .then(function () {
          return delWater(ids);
        })
        .then(() => {
          this.getList();
          this.msgSuccess("删除成功");
        });
    },
    /** 导出按钮操作 */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm("是否确认导出所有【请填写功能名称】数据项?", "警告", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(function () {
          return exportWater(queryParams);
        })
        .then((response) => {
          this.download(response.msg);
        });
    },
  },
};
</script>
