import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listSafeguard(query) {
  return request({
    url: '/buss/device/safeguard/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getSafeguard(id) {
  return request({
    url: '/buss/device/safeguard/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addSafeguard(data) {
  return request({
    url: '/buss/device/safeguard',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updateSafeguard(data) {
  return request({
    url: '/buss/device/safeguard',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delSafeguard(id) {
  return request({
    url: '/buss/device/safeguard/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportSafeguard(query) {
  return request({
    url: '/buss/device/safeguard/export',
    method: 'get',
    params: query
  })
}