import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listVedio(query) {
  return request({
    url: '/buss/safe/vedio/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getVedio(id) {
  return request({
    url: '/buss/safe/vedio/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addVedio(data) {
  return request({
    url: '/buss/safe/vedio',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updateVedio(data) {
  return request({
    url: '/buss/safe/vedio',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delVedio(id) {
  return request({
    url: '/buss/safe/vedio/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportVedio(query) {
  return request({
    url: '/buss/safe/vedio/export',
    method: 'get',
    params: query
  })
}