import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listPlan(query) {
  return request({
    url: '/buss/emergency/plan/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getPlan(id) {
  return request({
    url: '/buss/emergency/plan/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addPlan(data) {
  return request({
    url: '/buss/emergency/plan',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updatePlan(data) {
  return request({
    url: '/buss/emergency/plan',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delPlan(id) {
  return request({
    url: '/buss/emergency/plan/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportPlan(query) {
  return request({
    url: '/buss/emergency/plan/export',
    method: 'get',
    params: query
  })
}