import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listAssets(query) {
  return request({
    url: '/buss/workshop/assets/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getAssets(id) {
  return request({
    url: '/buss/workshop/assets/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addAssets(data) {
  return request({
    url: '/buss/workshop/assets',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updateAssets(data) {
  return request({
    url: '/buss/workshop/assets',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delAssets(id) {
  return request({
    url: '/buss/workshop/assets/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportAssets(query) {
  return request({
    url: '/buss/workshop/assets/export',
    method: 'get',
    params: query
  })
}