import request from '@/utils/request'
// 查询【请填写功能名称】列表
export async function dictInit(cdCode) {
  let keys=cdCode.split(",");
  let exists=true;
  for(let i=0;i<keys.length;i++){
    let value=localStorage["dict_"+keys[i]];
    if(value==undefined||value==null||value=="null"){
      exists=false;
    }
  }
  if(exists){
    return;
  }
  await request({
    url: '/buss/base/cache/dicts',
    method: 'get',
    params: {cdCode:cdCode}
  }).then(response => {
    for(let key in response.data){
      localStorage["dict_"+key]=JSON.stringify(response.data[key]);
    }
  });
}
export function dictCustom(cdCode) {
    let value=localStorage["dict_"+cdCode];
    value=JSON.parse(value);
    return value;
}

export function getDictValue(cdCode,dict) {
  let dictLabel="";
  for(let i=0;i<dict.length;i++){
    if(dict[i].dictValue==cdCode){
      dictLabel=dict[i].dictLabel;
    }
  }
  return dictLabel;
}
