<template>
  <div class="app-container">
    <el-form
      :model="queryParams"
      ref="queryForm"
      :inline="true"
      v-show="showSearch"
      label-width="68px"
    >
      <el-form-item label="终止日期" prop="sysHouseId">
        <el-input
          v-model="queryParams.sysHouseId"
          placeholder="请输入终止日期"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item>
        <el-button
          type="cyan"
          icon="el-icon-search"
          size="mini"
          @click="handleQuery"
          >搜索</el-button
        >
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"
          >重置</el-button
        >
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:electricity:add']"
          >新增</el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:electricity:edit']"
          >修改</el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:electricity:remove']"
          >删除</el-button
        >
      </el-col>
      <right-toolbar
        :showSearch.sync="showSearch"
        @queryTable="getList"
      ></right-toolbar>
    </el-row>

    <el-table
      v-loading="loading"
      :data="electricityList"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column
        label="本期度数"
        align="center"
        prop="elDegreesCurrrent"
      />
      <el-table-column label="上期度数" align="center" prop="elDegreesPre" />
      <el-table-column
        label="抄表日期"
        align="center"
        prop="elDegreesDate"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.elDegreesDate, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column label="用电度数" align="center" prop="elDegreesSum" />
      <el-table-column label="电费单价" align="center" prop="elDegreesPrice" />
      <el-table-column label="应缴金额" align="center" prop="elPayShould" />
      <el-table-column label="实际缴费" align="center" prop="elPayReality" />
      <el-table-column
        label="缴费日期"
        align="center"
        prop="elPayDate"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.elPayDate, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column label="缴费方式" align="center" prop="elPayType" />
      <el-table-column
        label="终止日期"
        align="center"
        prop="elContractEnd"
        width="180"
      >
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.elContractEnd, "{y}-{m}-{d}") }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        class-name="small-padding fixed-width"
        width="200px"
        fixed="right"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:electricity:edit']"
            >修改</el-button
          >
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:electricity:remove']"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!-- 添加或修改【请填写功能名称】对话框 -->
    <el-dialog :title="title" :visible.sync="open" width="700px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="上期度数" prop="elDegreesPre">
              <el-input
                v-model="form.elDegreesPre"
                placeholder="请输入上期度数"
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="本期度数" prop="elDegreesCurrrent">
              <el-input
                v-model="form.elDegreesCurrrent"
                placeholder="请输入本期度数"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="抄表日期" prop="elDegreesDate">
              <el-date-picker
                clearable
                size="small"
                style="width:100%"
                v-model="form.elDegreesDate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择抄表日期"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="用电度数" prop="elDegreesSum">
              <el-input
                v-model="form.elDegreesSum"
                placeholder="请输入用电度数"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12"> 
            <el-form-item label="电费单价" prop="elDegreesPrice">
              <el-input
                v-model="form.elDegreesPrice"
                placeholder="请输入电费单价"
              />
            </el-form-item>
        </el-col>
          <el-col :span="12">
            <el-form-item label="应缴金额" prop="elPayShould">
              <el-input
                v-model="form.elPayShould"
                placeholder="请输入应缴金额"
              />
            </el-form-item>
          </el-col>
        </el-row>
          <el-row>
          <el-col :span="12">
            <el-form-item label="实际缴费" prop="elPayReality">
              <el-input
                v-model="form.elPayReality"
                placeholder="请输入实际缴费"
              />
            </el-form-item>
        </el-col>
          <el-col :span="12">
            <el-form-item label="缴费日期" prop="elPayDate">
              <el-date-picker
                clearable
                size="small"
                style="width:100%"
                v-model="form.elPayDate"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择缴费日期"
              >
              </el-date-picker>
            </el-form-item>
            </el-col>
        </el-row>
          <el-row>
          <el-col :span="12">
            <el-form-item label="支付方式" prop="elPayType">
              <el-select v-model="form.elPayType" placeholder="请选择缴费方式" style="width:100%">
                <el-option label="请选择字典生成" value="" />
              </el-select>
            </el-form-item>
        </el-col>
          <el-col :span="12">
            <el-form-item label="终止日期" prop="elContractEnd">
              <el-date-picker
                clearable
                size="small"
                style="width:100%"
                v-model="form.elContractEnd"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择终止日期"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm">确 定</el-button>
        <el-button @click="cancel">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  listElectricity,
  getElectricity,
  delElectricity,
  addElectricity,
  updateElectricity,
  exportElectricity,
} from "@/api/buss/house/electricity";

export default {
  name: "Electricity",
  components: {},
  data() {
    return {
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 【请填写功能名称】表格数据
      electricityList: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        sysHouseId: null,
        sysLeaseId: null,
        elDegreesCurrrent: null,
        elDegreesPre: null,
        elDegreesDate: null,
        elDegreesSum: null,
        elDegreesPrice: null,
        elPayShould: null,
        elPayReality: null,
        elPayDate: null,
        elPayType: null,
        elContractEnd: null,
      },
      // 表单参数
      form: {},
      // 表单校验
      rules: {},
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /** 查询【请填写功能名称】列表 */
    getList() {
      this.loading = true;
      listElectricity(this.queryParams).then((response) => {
        this.electricityList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 取消按钮
    cancel() {
      this.open = false;
      this.reset();
    },
    // 表单重置
    reset() {
      this.form = {
        id: null,
        sysHouseId: null,
        sysLeaseId: null,
        elDegreesCurrrent: null,
        elDegreesPre: null,
        elDegreesDate: null,
        elDegreesSum: null,
        elDegreesPrice: null,
        elPayShould: null,
        elPayReality: null,
        elPayDate: null,
        elPayType: null,
        elContractEnd: null,
      };
      this.resetForm("form");
    },
    /** 搜索按钮操作 */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /** 重置按钮操作 */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 多选框选中数据
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id);
      this.single = selection.length !== 1;
      this.multiple = !selection.length;
    },
    /** 新增按钮操作 */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "房屋楼电费登记";
    },
    /** 修改按钮操作 */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids;
      getElectricity(id).then((response) => {
        this.form = response.data;
        this.open = true;
        this.title = "房屋楼电费登记";
      });
    },
    /** 提交按钮 */
    submitForm() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.form.id != null) {
            updateElectricity(this.form).then((response) => {
              this.msgSuccess("修改成功");
              this.open = false;
              this.getList();
            });
          } else {
            addElectricity(this.form).then((response) => {
              this.msgSuccess("新增成功");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /** 删除按钮操作 */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm(
        '是否确认删除房屋楼电费登记编号为"' + ids + '"的数据项?',
        "警告",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      )
        .then(function () {
          return delElectricity(ids);
        })
        .then(() => {
          this.getList();
          this.msgSuccess("删除成功");
        });
    },
    /** 导出按钮操作 */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm("是否确认导出所有【请填写功能名称】数据项?", "警告", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(function () {
          return exportElectricity(queryParams);
        })
        .then((response) => {
          this.download(response.msg);
        });
    },
  },
};
</script>
