import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listRealty(query) {
  return request({
    url: '/buss/workshop/realty/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getRealty(id) {
  return request({
    url: '/buss/workshop/realty/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addRealty(data) {
  return request({
    url: '/buss/workshop/realty',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updateRealty(data) {
  return request({
    url: '/buss/workshop/realty',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delRealty(id) {
  return request({
    url: '/buss/workshop/realty/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportRealty(query) {
  return request({
    url: '/buss/workshop/realty/export',
    method: 'get',
    params: query
  })
}