<template>
  <div class="app-container">
    <el-form :model="queryParams" ref="queryForm" :inline="true" v-show="showSearch" label-width="68px">
      <el-form-item label="厂房id" prop="sysWorkshopId">
        <el-input
          v-model="queryParams.sysWorkshopId"
          placeholder="请输入厂房id"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="姓名" prop="cwName">
        <el-input
          v-model="queryParams.cwName"
          placeholder="请输入姓名"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="性别" prop="cwSex">
        <el-select v-model="queryParams.cwSex" placeholder="请选择性别" clearable size="small">
          <el-option label="请选择字典生成" value="" />
        </el-select>
      </el-form-item>
      <el-form-item label="出生日期" prop="cwBirthday">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.cwBirthday"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择出生日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="职务" prop="cwDuty">
        <el-input
          v-model="queryParams.cwDuty"
          placeholder="请输入职务"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="电话" prop="cwTell">
        <el-input
          v-model="queryParams.cwTell"
          placeholder="请输入电话"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="照片" prop="cwPhoto">
        <el-input
          v-model="queryParams.cwPhoto"
          placeholder="请输入照片"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="劳动合同" prop="cwContract">
        <el-input
          v-model="queryParams.cwContract"
          placeholder="请输入劳动合同"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="工作证件" prop="cwDutyCertificate">
        <el-input
          v-model="queryParams.cwDutyCertificate"
          placeholder="请输入工作证件"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="入厂时间" prop="cwJoinDate">
        <el-date-picker clearable size="small" style="width: 200px"
          v-model="queryParams.cwJoinDate"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="选择入厂时间">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="cyan" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery">重置</el-button>
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:works:add']"
        >新增</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:works:edit']"
        >修改</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:works:remove']"
        >删除</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="warning"
          icon="el-icon-download"
          size="mini"
          @click="handleExport"
          v-hasPermi="['system:works:export']"
        >导出</el-button>
      </el-col>
	  <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar>
    </el-row>

    <el-table v-loading="loading" :data="worksList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="入厂时间" align="center" prop="id" />
      <el-table-column label="厂房id" align="center" prop="sysWorkshopId" />
      <el-table-column label="姓名" align="center" prop="cwName" />
      <el-table-column label="性别" align="center" prop="cwSex" />
      <el-table-column label="出生日期" align="center" prop="cwBirthday" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.cwBirthday, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="职务" align="center" prop="cwDuty" />
      <el-table-column label="电话" align="center" prop="cwTell" />
      <el-table-column label="照片" align="center" prop="cwPhoto" />
      <el-table-column label="劳动合同" align="center" prop="cwContract" />
      <el-table-column label="工作证件" align="center" prop="cwDutyCertificate" />
      <el-table-column label="入厂时间" align="center" prop="cwJoinDate" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.cwJoinDate, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:works:edit']"
          >修改</el-button>
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:works:remove']"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!-- 添加或修改【请填写功能名称】对话框 -->
    <el-dialog :title="title" :visible.sync="open" width="500px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="厂房id" prop="sysWorkshopId">
          <el-input v-model="form.sysWorkshopId" placeholder="请输入厂房id" />
        </el-form-item>
        <el-form-item label="姓名" prop="cwName">
          <el-input v-model="form.cwName" placeholder="请输入姓名" />
        </el-form-item>
        <el-form-item label="性别" prop="cwSex">
          <el-select v-model="form.cwSex" placeholder="请选择性别">
            <el-option label="请选择字典生成" value="" />
          </el-select>
        </el-form-item>
        <el-form-item label="出生日期" prop="cwBirthday">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.cwBirthday"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择出生日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="职务" prop="cwDuty">
          <el-input v-model="form.cwDuty" placeholder="请输入职务" />
        </el-form-item>
        <el-form-item label="电话" prop="cwTell">
          <el-input v-model="form.cwTell" placeholder="请输入电话" />
        </el-form-item>
        <el-form-item label="照片" prop="cwPhoto">
          <el-input v-model="form.cwPhoto" placeholder="请输入照片" />
        </el-form-item>
        <el-form-item label="劳动合同" prop="cwContract">
          <el-input v-model="form.cwContract" placeholder="请输入劳动合同" />
        </el-form-item>
        <el-form-item label="工作证件" prop="cwDutyCertificate">
          <el-input v-model="form.cwDutyCertificate" placeholder="请输入工作证件" />
        </el-form-item>
        <el-form-item label="入厂时间" prop="cwJoinDate">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.cwJoinDate"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择入厂时间">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm">确 定</el-button>
        <el-button @click="cancel">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { listWorks, getWorks, delWorks, addWorks, updateWorks, exportWorks } from "@/api/buss/workshop/works";

export default {
  name: "Works",
  components: {
  },
  data() {
    return {
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 【请填写功能名称】表格数据
      worksList: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        sysWorkshopId: null,
        cwName: null,
        cwSex: null,
        cwBirthday: null,
        cwDuty: null,
        cwTell: null,
        cwPhoto: null,
        cwContract: null,
        cwDutyCertificate: null,
        cwJoinDate: null
      },
      // 表单参数
      form: {},
      // 表单校验
      rules: {
      }
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /** 查询【请填写功能名称】列表 */
    getList() {
      this.loading = true;
      listWorks(this.queryParams).then(response => {
        this.worksList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 取消按钮
    cancel() {
      this.open = false;
      this.reset();
    },
    // 表单重置
    reset() {
      this.form = {
        id: null,
        sysWorkshopId: null,
        cwName: null,
        cwSex: null,
        cwBirthday: null,
        cwDuty: null,
        cwTell: null,
        cwPhoto: null,
        cwContract: null,
        cwDutyCertificate: null,
        cwJoinDate: null
      };
      this.resetForm("form");
    },
    /** 搜索按钮操作 */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /** 重置按钮操作 */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 多选框选中数据
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length!==1
      this.multiple = !selection.length
    },
    /** 新增按钮操作 */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "添加【请填写功能名称】";
    },
    /** 修改按钮操作 */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids
      getWorks(id).then(response => {
        this.form = response.data;
        this.open = true;
        this.title = "修改【请填写功能名称】";
      });
    },
    /** 提交按钮 */
    submitForm() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          if (this.form.id != null) {
            updateWorks(this.form).then(response => {
              this.msgSuccess("修改成功");
              this.open = false;
              this.getList();
            });
          } else {
            addWorks(this.form).then(response => {
              this.msgSuccess("新增成功");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /** 删除按钮操作 */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm('是否确认删除【请填写功能名称】编号为"' + ids + '"的数据项?', "警告", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(function() {
          return delWorks(ids);
        }).then(() => {
          this.getList();
          this.msgSuccess("删除成功");
        })
    },
    /** 导出按钮操作 */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm('是否确认导出所有【请填写功能名称】数据项?', "警告", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(function() {
          return exportWorks(queryParams);
        }).then(response => {
          this.download(response.msg);
        })
    }
  }
};
</script>
