import request from '@/utils/request'

// 查询【请填写功能名称】列表
export function listRepair(query) {
  return request({
    url: '/buss/device/repair/list',
    method: 'get',
    params: query
  })
}

// 查询【请填写功能名称】详细
export function getRepair(id) {
  return request({
    url: '/buss/device/repair/' + id,
    method: 'get'
  })
}

// 新增【请填写功能名称】
export function addRepair(data) {
  return request({
    url: '/buss/device/repair',
    method: 'post',
    data: data
  })
}

// 修改【请填写功能名称】
export function updateRepair(data) {
  return request({
    url: '/buss/device/repair',
    method: 'put',
    data: data
  })
}

// 删除【请填写功能名称】
export function delRepair(id) {
  return request({
    url: '/buss/device/repair/' + id,
    method: 'delete'
  })
}

// 导出【请填写功能名称】
export function exportRepair(query) {
  return request({
    url: '/buss/device/repair/export',
    method: 'get',
    params: query
  })
}