<template>
  <div class="app-container">
    <el-form :model="queryParams" ref="queryForm" :inline="true" v-show="showSearch" label-width="68px">
      <el-form-item label="用户名" prop="ctName">
        <el-input
          v-model="queryParams.ctName"
          placeholder="请输入用户名"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        />
      </el-form-item>
      <el-form-item label="类型" prop="ctType">
        <el-select v-model="queryParams.ctType" placeholder="请选择类型：字典" clearable size="small">
          <el-option
            v-for="dict in bindData.ctType"
            :key="dict.dictValue"
            :label="dict.dictLabel"
            :value="dict.dictValue"
          />
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="cyan" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery">重置</el-button>
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:contacts:add']"
        >新增</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:contacts:edit']"
        >修改</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:contacts:remove']"
        >删除</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="warning"
          icon="el-icon-download"
          size="mini"
          @click="handleExport"
          v-hasPermi="['system:contacts:export']"
        >导出</el-button>
      </el-col>
	  <right-toolbar :showSearch.sync="showSearch" @queryTable="getList"></right-toolbar>
    </el-row>

    <el-table v-loading="loading" :data="contactsList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="用户名" align="center" prop="ctName" />
      <el-table-column label="性别" align="center" prop="ctSex">
        <template slot-scope="scope">
          <span>{{ getDictValue(scope.row.ctSex,bindData.ctSex)}}</span>
        </template>
      </el-table-column>
      <el-table-column label="类型" align="center" prop="ctType">
        <template slot-scope="scope">
          <span>{{ getDictValue(scope.row.ctType,bindData.ctType)}}</span>
        </template>
      </el-table-column>
      <el-table-column label="职务" align="center" prop="ctJob" />
      <el-table-column label="电话" align="center" prop="ctTell" />
      <el-table-column label="修改时间" align="center" prop="sysModifiedTime" width="180">
        <template slot-scope="scope">
          <span>{{ parseTime(scope.row.sysModifiedTime, '{y}-{m}-{d}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:contacts:edit']"
          >修改</el-button>
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:contacts:remove']"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    
    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!-- 添加或修改【请填写功能名称】对话框 -->
    <el-dialog :title="title" :visible.sync="open" width="500px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="用户名" prop="ctName">
          <el-input v-model="form.ctName" placeholder="请输入用户名" />
        </el-form-item>
        <el-form-item label="性别" prop="ctSex">
          <el-select v-model="form.ctSex" placeholder="请选择性别">
            <el-option
            v-for="dict in bindData.ctSex"
            :key="dict.dictValue"
            :label="dict.dictLabel"
            :value="dict.dictValue"
          />
          </el-select>
        </el-form-item>
        <el-form-item label="类型" prop="ctType">
          <el-select v-model="form.ctType" placeholder="请选择类型">
            <el-option
            v-for="dict in bindData.ctType"
            :key="dict.dictValue"
            :label="dict.dictLabel"
            :value="dict.dictValue"
          />
          </el-select>
        </el-form-item>
        <el-form-item label="职务" prop="ctJob">
          <el-input v-model="form.ctJob" placeholder="请输入职务" />
        </el-form-item>
        <el-form-item label="电话" prop="ctTell">
          <el-input v-model="form.ctTell" placeholder="请输入电话" />
        </el-form-item>
        <el-form-item label="修改时间" prop="sysModifiedTime">
          <el-date-picker clearable size="small" style="width: 200px"
            v-model="form.sysModifiedTime"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择修改时间">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm">确 定</el-button>
        <el-button @click="cancel">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { listContacts, getContacts, delContacts, addContacts, updateContacts, exportContacts } from "@/api/buss/base/contacts";
import { dictInit,dictCustom,getDictValue } from "@/api/buss/base/cache";

export default {
  name: "Contacts",
  components: {
  },
  data() {
    return {
      bindData:{
        ctType:[],
        ctSex:[]
      },
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 【请填写功能名称】表格数据
      contactsList: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        ctName: null,
        ctSex: null,
        ctType: null,
        ctJob: null,
        ctTell: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null
      },
      // 表单参数
      form: {},
      // 表单校验
      rules: {
      }
    };
  },
  created() {
    this.dictInit();
    this.getList();
  },
  methods: {
    async dictInit(){
      await dictInit("contract_type,contract_sex");
      this.bindData.ctType=dictCustom("contract_type");
      this.bindData.ctSex=dictCustom("contract_sex");
    }, 
    getDictValue(cdCode,dict){
      return getDictValue(cdCode,dict);
    },
    /** 查询【请填写功能名称】列表 */
    getList() {
      this.loading = true;
      listContacts(this.queryParams).then(response => {
        this.contactsList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 取消按钮
    cancel() {
      this.open = false;
      this.reset();
    },
    // 表单重置
    reset() {
      this.form = {
        id: null,
        ctName: null,
        ctSex: null,
        ctType: null,
        ctJob: null,
        ctTell: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null
      };
      this.resetForm("form");
    },
    /** 搜索按钮操作 */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /** 重置按钮操作 */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 多选框选中数据
    handleSelectionChange(selection) {
      this.ids = selection.map(item => item.id)
      this.single = selection.length!==1
      this.multiple = !selection.length
    },
    /** 新增按钮操作 */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "添加【请填写功能名称】";
    },
    /** 修改按钮操作 */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids
      getContacts(id).then(response => {
        this.form = response.data;
        this.open = true;
        this.title = "修改【请填写功能名称】";
      });
    },
    /** 提交按钮 */
    submitForm() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          if (this.form.id != null) {
            updateContacts(this.form).then(response => {
              this.msgSuccess("修改成功");
              this.open = false;
              this.getList();
            });
          } else {
            addContacts(this.form).then(response => {
              this.msgSuccess("新增成功");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /** 删除按钮操作 */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm('是否确认删除【请填写功能名称】编号为"' + ids + '"的数据项?', "警告", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(function() {
          return delContacts(ids);
        }).then(() => {
          this.getList();
          this.msgSuccess("删除成功");
        })
    },
    /** 导出按钮操作 */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm('是否确认导出所有【请填写功能名称】数据项?', "警告", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(function() {
          return exportContacts(queryParams);
        }).then(response => {
          this.download(response.msg);
        })
    }
  }
};
</script>
