<template>
  <div class="app-container">
    <el-form
      :model="queryParams"
      ref="queryForm"
      :inline="true"
      v-show="showSearch"
      label-width="68px"
    >
      <el-form-item label="楼层" prop="cpFloor">
        <el-select
          v-model="queryParams.cpFloor"
          placeholder="请选择楼层"
          style="width: 100%"
        >
          <el-option label="主楼层一" value="1" />
          <el-option label="主楼层二" value="2" />
          <el-option label="主楼层三" value="3" />
          <el-option label="主楼层四" value="4" />
          <el-option label="主楼层五" value="5" />
          <el-option label="主楼层六" value="6" />
          <el-option label="主楼层七" value="7" />
          <el-option label="侧楼一层" value="8" />
          <el-option label="侧楼二层" value="9" />
          <el-option label="侧楼三层" value="10" />
        </el-select>
        <!-- <el-input
          v-model="queryParams.cpFloor"
          placeholder="请输入楼层"
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
        /> -->
      </el-form-item>
      <el-form-item label="状态" prop="cpStatus">
        <el-select
          v-model="queryParams.cpStatus"
          placeholder="请选择状态"
          clearable
          size="small"
        >
          <el-option label="已租" value="0" />
          <el-option label="未租" value="1" />
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button
          type="cyan"
          icon="el-icon-search"
          size="mini"
          @click="handleQuery"
          >搜索</el-button
        >
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"
          >重置</el-button
        >
      </el-form-item>
    </el-form>

    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button
          type="primary"
          icon="el-icon-plus"
          size="mini"
          @click="handleAdd"
          v-hasPermi="['system:assets:add']"
          >新增</el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="success"
          icon="el-icon-edit"
          size="mini"
          :disabled="single"
          @click="handleUpdate"
          v-hasPermi="['system:assets:edit']"
          >修改</el-button
        >
      </el-col>
      <el-col :span="1.5">
        <el-button
          type="danger"
          icon="el-icon-delete"
          size="mini"
          :disabled="multiple"
          @click="handleDelete"
          v-hasPermi="['system:assets:remove']"
          >删除</el-button
        >
      </el-col>
      <right-toolbar
        :showSearch.sync="showSearch"
        @queryTable="getList"
      ></right-toolbar>
    </el-row>

    <el-table
      v-loading="loading"
      :data="assetsList"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" />
      <el-table-column label="所属楼层" align="center" prop="cpFloor" />
      <el-table-column label="房间号" align="center" prop="cpNumber" />
      <el-table-column label="户型" align="center" prop="cpType" />
      <el-table-column label="面积" align="center" prop="cpArea" />
      <el-table-column label="用途" align="center" prop="cpNature" />
      <el-table-column label="状态" align="center" prop="cpStatus" />
      <el-table-column label="房屋描述" align="center" prop="cpContent" />
      <el-table-column label="产权人" align="center" prop="cpPropertyOwner" />
      <el-table-column
        label="产权证件"
        align="center"
        prop="cpCertificateTitle"
      />
      <el-table-column label="房间照片" align="center" prop="cpImage" />
      <el-table-column
        label="操作"
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['system:assets:edit']"
            >修改</el-button
          >
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['system:assets:remove']"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="queryParams.pageNum"
      :limit.sync="queryParams.pageSize"
      @pagination="getList"
    />

    <!-- 添加或修改【请填写功能名称】对话框 -->
    <el-dialog :title="title" :visible.sync="open" width="750px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="所属楼层" prop="cpFloor">
              <el-select
                v-model="form.cpFloor"
                placeholder="请选择楼层"
                style="width: 100%"
              >
                <el-option label="主楼层一" value="1" />
                <el-option label="主楼层二" value="2" />
                <el-option label="主楼层三" value="3" />
                <el-option label="主楼层四" value="4" />
                <el-option label="主楼层五" value="5" />
                <el-option label="主楼层六" value="6" />
                <el-option label="主楼层七" value="7" />
                <el-option label="侧楼一层" value="8" />
                <el-option label="侧楼二层" value="9" />
                <el-option label="侧楼三层" value="10" />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="房间号" prop="cpNumber">
              <el-input v-model="form.cpNumber" placeholder="请输入房间号" />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="户型" prop="cpType">
              <el-select
                v-model="form.cpType"
                placeholder="请选择户型"
                style="width: 100%"
              >
                <el-option label="一室" value="1" />
                <el-option label="二室" value="2" />
                <el-option label="三室" value="3" />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="面积" prop="cpArea">
              <el-input v-model="form.cpArea" placeholder="请输入面积" />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="用途" prop="cpNature">
              <el-input v-model="form.cpNature" placeholder="请输入用途性质" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="状态" prop="cpStatus">
              <el-select
                v-model="form.cpStatus"
                placeholder="请选择状态"
                style="width: 100%"
              >
                <el-option label="已租" value="0" />
                <el-option label="未租" value="1" />
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="产权人" prop="cpPropertyOwner">
              <el-input
                v-model="form.cpPropertyOwner"
                placeholder="请输入产权人"
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="产权证件" prop="cpCertificateTitle">
              <el-input
                v-model="form.cpCertificateTitle"
                placeholder="请输入产权证"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="房屋图片">
              <uploadImage v-model="form.cpImage" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="更新日期" prop="cpUpdateDate">
              <el-date-picker
                clearable
                size="small"
                v-model="form.cpUpdateDate"
                type="date"
                style="width: 100%"
                value-format="yyyy-MM-dd"
                placeholder="选择更新日期"
              >
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="房屋描述">
              <el-input
                type="textarea"
                v-model="form.cpContent"
                :autosize="{ minRows: 5, maxRows: 10 }"
                maxlength="2000"
                show-word-limit
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm">确 定</el-button>
        <el-button @click="cancel">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  listAssets,
  getAssets,
  delAssets,
  addAssets,
  updateAssets,
  exportAssets,
} from "@/api/buss/house/assets";
import UploadImage from "@/components/UploadImage";
import Editor from "@/components/Editor";

export default {
  name: "Assets",
  components: {
    UploadImage,
    Editor,
  },
  data() {
    return {
      // 遮罩层
      loading: true,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 【请填写功能名称】表格数据
      assetsList: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        cpNumber: null,
        cpFloor: null,
        cpType: null,
        cpArea: null,
        cpNature: null,
        cpStatus: null,
        cpPropertyOwner: null,
        cpCertificateTitle: null,
        cpUpdateDate: null,
        cpImage: null,
        cpContent: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null,
      },
      // 表单参数
      form: {},
      // 表单校验
      rules: {},
    };
  },
  created() {
    this.getList();
  },
  methods: {
    /** 查询【请填写功能名称】列表 */
    getList() {
      this.loading = true;
      listAssets(this.queryParams).then((response) => {
        this.assetsList = response.rows;
        this.total = response.total;
        this.loading = false;
      });
    },
    // 取消按钮
    cancel() {
      this.open = false;
      this.reset();
    },
    // 表单重置
    reset() {
      this.form = {
        id: null,
        cpNumber: null,
        cpFloor: null,
        cpType: null,
        cpArea: null,
        cpNature: null,
        cpStatus: null,
        cpPropertyOwner: null,
        cpCertificateTitle: null,
        cpUpdateDate: null,
        cpImage: null,
        cpContent: null,
        sysCreateTime: null,
        sysCreatorId: null,
        sysModifiedId: null,
        sysModifiedTime: null,
      };
      this.resetForm("form");
    },
    /** 搜索按钮操作 */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /** 重置按钮操作 */
    resetQuery() {
      this.resetForm("queryForm");
      this.handleQuery();
    },
    // 多选框选中数据
    handleSelectionChange(selection) {
      this.ids = selection.map((item) => item.id);
      this.single = selection.length !== 1;
      this.multiple = !selection.length;
    },
    /** 新增按钮操作 */
    handleAdd() {
      this.reset();
      this.open = true;
      this.title = "房屋楼资产登记";
    },
    /** 修改按钮操作 */
    handleUpdate(row) {
      this.reset();
      const id = row.id || this.ids;
      getAssets(id).then((response) => {
        this.form = response.data;
        this.open = true;
        this.title = "房屋楼资产登记";
      });
    },
    /** 提交按钮 */
    submitForm() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.form.id != null) {
            updateAssets(this.form).then((response) => {
              this.msgSuccess("修改成功");
              this.open = false;
              this.getList();
            });
          } else {
            addAssets(this.form).then((response) => {
              this.msgSuccess("新增成功");
              this.open = false;
              this.getList();
            });
          }
        }
      });
    },
    /** 删除按钮操作 */
    handleDelete(row) {
      const ids = row.id || this.ids;
      this.$confirm(
        '是否确认删除房屋楼资产登记编号为"' + ids + '"的数据项?',
        "警告",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      )
        .then(function () {
          return delAssets(ids);
        })
        .then(() => {
          this.getList();
          this.msgSuccess("删除成功");
        });
    },
    /** 导出按钮操作 */
    handleExport() {
      const queryParams = this.queryParams;
      this.$confirm("是否确认导出所有【请填写功能名称】数据项?", "警告", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(function () {
          return exportAssets(queryParams);
        })
        .then((response) => {
          this.download(response.msg);
        });
    },
  },
};
</script>
